*{
  margin:0px;
  padding:0px;
}

/* background image */
body{
background-image: url('/Users/guillianprince/Desktop/ecommercereact/ecommercereactv1/src/img/2.jpg');
 background-position: center;
 background-repeat: no-repeat;
 background-size: cover;
 min-height: 100%;
 max-width: 100%;
}

/* Contact information */
p{
  color: gray;
  padding: 6px;
  margin: 3px; 
}

h1{
  color: black;
  text-align: center;
  font-family: "raleway";
  font-weight:1px;
  font-size:36pt;
  /* margin-top:5vw; */
}

h2{
  color: black;
  text-align: center;
  font-family: "raleway"; 
  font-size: 14pt;
}

/* Contact Form */

input[type=text],textarea{
  width: 100%;
  padding: 12px;
  border: 1px solid white;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type=tel],textarea{
  width: 100%;
  padding: 12px;
  border: 1px solid white;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

.email{
  width: 100%;
  padding: 12px;
  border: 1px solid white;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical; 
  background-color: white;
}

.message{
height:160px; 
width: 100%; 
padding: 12px;
border: 1px solid #ccc;
border-radius: 4px;
box-sizing: border-box;
margin-top: 6px;
margin-bottom: 16px;
resize: vertical;
}

input[type=submit] {
  background-color:white;
  color: #442f2e;
  font-weight: bold;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: #d3d0d0;
}

.contact{
  background-color:#746c6c;
  opacity: 0.8;
  padding: 20px;
  width: 50%;
  margin: auto;
  margin-top: 2%;
  margin-bottom: 5vw;
  font-family:"raleway";
  font-weight: 3px;
  color:white;
  border-radius: 30px;
}



@media all and (max-width: 480px) {
  .hero h2 {
    font-size: 3.5em;
  }

  .form-name,
  .email,
  .message,
  .submit,
  .phone {
    width: 300px;
  }
}
